@tailwind base;
@tailwind components;
@tailwind utilities;

/* Mobile and Tablet Responsive Fixes */
* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden; /* Prevent horizontal scroll */
  -webkit-overflow-scrolling: touch; /* Smooth scrolling on iOS */
  -webkit-tap-highlight-color: transparent; /* Remove tap highlight on mobile */
}

body {
  position: relative;
  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

/* Prevent horizontal overflow */
#root {
  width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
}

/* Container fixes for mobile */
.container {
  width: 100%;
  max-width: 100%;
  padding-left: 1rem;
  padding-right: 1rem;
}

@media (min-width: 640px) {
  .container {
    max-width: 640px;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

@media (min-width: 1024px) {
  .container {
    max-width: 1024px;
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

@media (min-width: 1536px) {
  .container {
    max-width: 1536px;
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

/* Custom animations */
@keyframes blob {
  0% {
    transform: translate(0px, 0px) scale(1);
  }
  33% {
    transform: translate(30px, -50px) scale(1.1);
  }
  66% {
    transform: translate(-20px, 20px) scale(0.9);
  }
  100% {
    transform: translate(0px, 0px) scale(1);
  }
}

.animate-blob {
  animation: blob 7s infinite;
}

.animation-delay-2000 {
  animation-delay: 2s;
}

.animation-delay-4000 {
  animation-delay: 4s;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #3b82f6, #8b5cf6);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(to bottom, #2563eb, #7c3aed);
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}

/* Line clamp utility */
.line-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

/* Focus styles */
*:focus {
  outline: none;
}

/* Custom selection */
::selection {
  background: linear-gradient(to right, #3b82f6, #8b5cf6);
  color: white;
}

/* Loading animation */
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.animate-spin {
  animation: spin 1s linear infinite;
}

/* Pulse animation */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* Gradient text */
.bg-gradient-text {
  background: linear-gradient(to right, #3b82f6, #8b5cf6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Glass effect */
.glass {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Hover effects */
.hover-lift {
  transition: transform 0.2s ease-in-out;
}

.hover-lift:hover {
  transform: translateY(-4px);
}

/* Custom button styles */
.btn-primary {
  @apply px-6 py-3 bg-gradient-to-r from-blue-500 to-purple-600 text-white rounded-lg font-medium hover:shadow-lg transition-all duration-200;
}

.btn-secondary {
  @apply px-6 py-3 border-2 border-gray-300 text-gray-700 rounded-lg font-medium hover:border-blue-500 hover:text-blue-500 transition-all duration-200;
}

/* Card styles */
.card {
  @apply bg-white rounded-xl shadow-lg hover:shadow-xl transition-all duration-300;
}

/* Section spacing */
.section-padding {
  @apply py-20;
}

/* Container max width */
.container {
  @apply max-w-7xl mx-auto;
}

/* Responsive text */
.text-responsive {
  @apply text-2xl md:text-3xl lg:text-4xl;
}

/* Custom shadows */
.shadow-custom {
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.shadow-custom-lg {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

/* Mobile-specific fixes */
@media (max-width: 768px) {
  /* PROFESSIONAL MOBILE FIXES */
  
  /* Reset everything */
  * {
    box-sizing: border-box;
  }

  html, body {
    width: 100vw !important;
    max-width: 100vw !important;
    margin: 0 !important;
    padding: 0 !important;
    overflow-x: hidden !important;
  }

  #root {
    width: 100vw !important;
    max-width: 100vw !important;
    margin: 0 !important;
    padding: 0 !important;
    overflow-x: hidden !important;
  }

  /* Fix all containers and sections */
  .container, section, div {
    width: 100% !important;
    max-width: 100% !important;
    margin: 0 !important;
    padding-left: 1rem !important;
    padding-right: 1rem !important;
  }

  /* Fix header */
  header {
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    right: 0 !important;
    width: 100vw !important;
    z-index: 1000 !important;
    background: rgba(15, 23, 42, 0.95) !important;
    backdrop-filter: blur(10px) !important;
  }

  header .container {
    width: 100% !important;
    max-width: 100% !important;
    margin: 0 !important;
    padding: 1rem !important;
  }

  /* Fix hero section */
  #home {
    width: 100vw !important;
    max-width: 100vw !important;
    padding-top: 120px !important;
    min-height: 100vh !important;
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    margin: 0 !important;
  }

  #home .relative {
    width: 100% !important;
    max-width: 100% !important;
    padding: 1rem !important;
    text-align: center !important;
    margin: 0 auto !important;
  }

  /* Fix text sizes - NO OVERLAPPING */
  #home h1 {
    font-size: 1.5rem !important;
    line-height: 1.4 !important;
    margin-bottom: 1.5rem !important;
    word-wrap: break-word !important;
    text-align: center !important;
    padding: 0 0.5rem !important;
  }

  #home .text-xl {
    font-size: 0.9rem !important;
    line-height: 1.5 !important;
    margin-bottom: 1.5rem !important;
    text-align: center !important;
    padding: 0 0.5rem !important;
  }

  #home .text-lg {
    font-size: 0.8rem !important;
    line-height: 1.6 !important;
    margin-bottom: 2rem !important;
    text-align: center !important;
    padding: 0 0.5rem !important;
  }

  /* Fix buttons - NO OVERLAPPING */
  #home .flex.space-x-4 {
    flex-direction: column !important;
    gap: 1.5rem !important;
    align-items: center !important;
    width: 100% !important;
    margin-top: 2rem !important;
  }

  #home .flex.space-x-4 > * {
    width: 100% !important;
    max-width: 280px !important;
    margin: 0 !important;
    padding: 0.75rem 1rem !important;
    font-size: 0.9rem !important;
  }

  /* Fix social icons - NO OVERLAPPING */
  #home .flex.space-x-6 {
    justify-content: center !important;
    gap: 2rem !important;
    margin-top: 3rem !important;
    width: 100% !important;
  }

  /* Fix floating badge */
  #home .absolute.bottom-4.right-4 {
    position: fixed !important;
    bottom: 2rem !important;
    right: 1rem !important;
    z-index: 100 !important;
    font-size: 0.7rem !important;
    padding: 0.4rem 0.8rem !important;
    width: auto !important;
  }

  /* Fix scroll up button */
  .fixed.bottom-4.right-4 {
    width: 2.5rem !important;
    height: 2.5rem !important;
    border-radius: 50% !important;
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    font-size: 1rem !important;
    z-index: 100 !important;
    background: rgba(59, 130, 246, 0.9) !important;
    backdrop-filter: blur(10px) !important;
  }

  /* Hide problematic elements */
  #home .absolute.w-1.h-1,
  #home .absolute.top-32,
  #home .absolute.top-48 {
    display: none !important;
  }

  /* Fix background */
  #home .absolute.inset-0 {
    z-index: -1 !important;
  }

  /* Fix language dropdown */
  .absolute {
    max-width: calc(100vw - 2rem) !important;
    min-width: 200px !important;
    z-index: 1000 !important;
  }

  /* Fix mobile menu */
  .md\\:hidden {
    position: absolute !important;
    top: 100% !important;
    left: 0 !important;
    right: 0 !important;
    width: 100% !important;
    z-index: 50 !important;
  }

  /* Fix grid layouts - NO OVERLAPPING */
  .grid {
    gap: 1rem !important;
    width: 100% !important;
  }

  /* Fix technology grid specifically */
  .grid.grid-cols-2.sm\\:grid-cols-3.md\\:grid-cols-4 {
    grid-template-columns: repeat(2, 1fr) !important;
    gap: 0.75rem !important;
  }

  .grid-cols-1.md\\:grid-cols-2,
  .grid-cols-1.md\\:grid-cols-3 {
    grid-template-columns: repeat(1, minmax(0, 1fr)) !important;
  }

  /* Fix technology cards */
  .grid.grid-cols-2.sm\\:grid-cols-3.md\\:grid-cols-4 > * {
    padding: 0.75rem !important;
    font-size: 0.8rem !important;
    min-height: auto !important;
  }

  .grid.grid-cols-2.sm\\:grid-cols-3.md\\:grid-cols-4 .text-3xl {
    font-size: 1.5rem !important;
    margin-bottom: 0.5rem !important;
  }

  .grid.grid-cols-2.sm\\:grid-cols-3.md\\:grid-cols-4 .font-semibold {
    font-size: 0.75rem !important;
    margin-bottom: 0.25rem !important;
  }

  .grid.grid-cols-2.sm\\:grid-cols-3.md\\:grid-cols-4 .text-sm {
    font-size: 0.7rem !important;
  }

  /* Fix flex layouts */
  .flex-wrap {
    flex-wrap: wrap !important;
  }

  .gap-4 {
    gap: 1.5rem !important;
  }

  .gap-6 {
    gap: 2rem !important;
  }

  /* Fix spacing - NO OVERLAPPING */
  .space-x-4 {
    gap: 1rem !important;
  }

  .space-x-4 > * {
    margin-left: 0 !important;
  }

  .space-y-2 > * + * {
    margin-top: 1rem !important;
  }

  .space-y-4 > * + * {
    margin-top: 1.5rem !important;
  }

  /* Fix touch targets */
  button, a {
    min-height: 48px !important;
    min-width: 48px !important;
  }

  /* Prevent zoom */
  input, textarea, select {
    font-size: 16px !important;
  }

  /* Fix forms - NO OVERLAPPING */
  form {
    width: 100% !important;
    max-width: 100% !important;
  }

  form input,
  form textarea,
  form select {
    width: 100% !important;
    max-width: 100% !important;
    margin-bottom: 1rem !important;
    padding: 0.75rem !important;
    font-size: 16px !important;
  }

  /* Fix all text elements */
  p, span, div {
    word-wrap: break-word !important;
    overflow-wrap: break-word !important;
  }

  /* Fix padding for all sections */
  section {
    padding-top: 2rem !important;
    padding-bottom: 2rem !important;
  }

  /* Fix margins for all elements */
  * {
    margin-left: 0 !important;
    margin-right: 0 !important;
  }

  /* Fix progress bars - NO OVERLAPPING */
  .bg-gradient-to-r.from-blue-500.via-purple-500.to-pink-500 {
    height: 0.5rem !important;
    border-radius: 0.25rem !important;
    overflow: hidden !important;
  }

  /* Fix skill level text */
  .text-sm.text-gray-400,
  .text-sm.text-gray-600 {
    font-size: 0.7rem !important;
    line-height: 1.2 !important;
  }

  /* Fix skill names */
  .font-semibold.text-white,
  .font-semibold.text-gray-900 {
    font-size: 0.8rem !important;
    line-height: 1.3 !important;
    word-wrap: break-word !important;
  }

  /* Fix all text elements to prevent overlapping */
  .text-2xl {
    font-size: 1.25rem !important;
    line-height: 1.4 !important;
  }

  .text-xl {
    font-size: 1rem !important;
    line-height: 1.4 !important;
  }

  .text-lg {
    font-size: 0.9rem !important;
    line-height: 1.5 !important;
  }

  /* Fix section spacing */
  .py-16.md\\:py-20 {
    padding-top: 2rem !important;
    padding-bottom: 2rem !important;
  }

  /* Fix card spacing */
  .p-8.rounded-2xl {
    padding: 1.5rem !important;
  }

  .p-4.rounded-xl {
    padding: 0.75rem !important;
  }
}

/* Tablet-specific fixes */
@media (min-width: 769px) and (max-width: 1024px) {
  .section-padding {
    @apply py-16;
  }
}

/* Landscape orientation fixes */
@media (orientation: landscape) and (max-height: 500px) {
  .section-padding {
    @apply py-8;
  }
}

/* High DPI displays */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  /* Ensure crisp rendering on high DPI displays */
  img, svg {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
  }
}
